import React from "react";
import {TimingAll} from "./TimingAll";
import {TimingBoxplot} from "./TimingBoxplot";
import GeneralDistribution from "./GeneralDistribution";
import {getGeneralDistribution, getHeatmapCohortData, getTimingAll, getTimingBoxplotData} from "./data";
import {inputData} from "./dataset";
import {HeatMapCohorts} from "./HeatmapCohorts";


class App extends React.Component<object, object> {


    timingsBoxplotData = getTimingBoxplotData(inputData);
    heatMapCohortData = getHeatmapCohortData(inputData);
    timingAllData = getTimingAll(inputData);
    generalDistributionData = getGeneralDistribution(inputData);

    render() {
        return (
            <div>
                <GeneralDistribution generalDistributionData={this.generalDistributionData}/>
                <TimingAll timingAllData={this.timingAllData}/>
                <TimingBoxplot timingsBoxplotData={this.timingsBoxplotData}/>
                <HeatMapCohorts heatmapCohortData={this.heatMapCohortData}/>
            </div>
        );
    }
}

export default App;
