import React from "react";

import {ReactECharts, ReactEChartsProps} from "./React-ECharts";
import {SetOptionOpts} from "echarts";


export function HeatMapCohorts(props: any) {

    const echartOption: ReactEChartsProps["option"] = {
            title: {
              text: 'Heatmap of conversions into consequent actions'
            },
            tooltip: {
                position: 'top'
            },
            grid: {
                height: '50%',
                top: '10%'
            },
            dataZoom: [
                {
                    type: 'inside',
                    endValue: 10
                },
                {
                    type: 'slider',
                    endValue: 10
                }
            ],
            xAxis: {
                type: 'category',
                data: props.heatmapCohortData.x,
                splitArea: {
                    show: true
                },
                name: 'Months after acquisition'
            },
            yAxis: {
                type: 'category',
                data: props.heatmapCohortData.y,
                splitArea: {
                    show: true
                },
                name: 'Date of acquisition'
            },
            visualMap: {
                min: 0,
                max: props.heatmapCohortData.maxValue,
                calculable: true,
                orient: 'horizontal',
                precision: 5,
                left: 'center',
                bottom: '15%'
            },
            series: [
                {
                    name: 'Punch Card',
                    type: 'heatmap',
                    data: props.heatmapCohortData.data,
                    label: {
                        show: false
                    },
                    emphasis: {
                        itemStyle: {
                            shadowBlur: 10,
                            shadowColor: 'rgba(0, 0, 0, 0.5)'
                        }
                    }
                }
            ]
        };

        const echartsSettings: SetOptionOpts = {replaceMerge: ["series"]};
        return (
            <>
                    <ReactECharts option={echartOption}
                                  settings={echartsSettings}
                                  style={{height: '600px', width: '100%'}}/>
            </>
        );
}

