import React from "react";
import {ReactECharts, ReactEChartsProps} from "./React-ECharts";
import {SetOptionOpts} from "echarts";

export function TimingAll(props: any) {


    const echartOption: ReactEChartsProps["option"] =  {
            title: {
                text: 'Distribution of timing between subsequent actions'
            },
            dataset: {
                source: props.timingAllData,
                sourceHeader: true
            },
            legend: {},
            tooltip: {},
            dataZoom: [
                {
                    type: 'inside'
                },
                {
                    type: 'slider'
                }
            ],
            xAxis: {type: 'category', min: 0,  name: "# of days"},
            // Declare a y-axis (value axis).
            yAxis: {name: "# of actions"},
            // Declare several 'bar' series,
            // every series will auto-map to each rows by default.
            series: [
                {
                    type: "bar",
                    encode: {
                        x: "days",
                        y: "number_of_users"
                    },
                    large: true
                }
            ]
    }

        const echartsSettings: SetOptionOpts = {replaceMerge: ["series"]};
        return (
            <>
                <div>
                    <ReactECharts option={echartOption}
                                  settings={echartsSettings}
                                  style={{height: '600px', width: '100%'}}/>
                </div>
            </>
        );

}
