import React from "react";
import {ReactECharts, ReactEChartsProps} from "./React-ECharts";
import {SetOptionOpts} from "echarts";


const initialState = {
    isRelatedGraph: true,
    isCompareWithGeneral: false
};

export type ControlState = Readonly<typeof initialState>;

class GeneralDistribution extends React.Component<any, ControlState> {
    readonly state: ControlState = initialState;

    barSeries = (name: string) => {
        return {
            type: "bar",
            encode: {
                x: "action_number",
                y: name
            }
        };
    }

    echartOption = (): ReactEChartsProps["option"] => {

        const barSeries: any = this.barSeries(   //TODO fix types
            this.state.isRelatedGraph ? "number_of_users_related": "number_of_users_sum");
        const generalSeries: any = this.barSeries("number_of_users_related_general");

        const series: any = this.state.isCompareWithGeneral ? [barSeries, generalSeries] : [barSeries];

        return {
            title: {
                text: `You lost ${this.props.generalDistributionData.lost_users_after_first_action_related}% (approx. ${this.props.generalDistributionData.lost_users_after_first_action}) users after their first successful action`
            },
            dataset: {
                source: this.props.generalDistributionData.dataset,
                sourceHeader: true
            },
            tooltip: {},
            dataZoom: [
                {
                    type: 'inside',
                    endValue: 4
                },
                {
                    type: 'slider',
                    endValue: 4
                }
            ],
            xAxis: {type: 'category',  name: "# of action"},
            // Declare a y-axis (value axis).
            yAxis: {name: "# of users"},
            // Declare several 'bar' series,
            // every series will auto-map to each rows by default.
            series: series
        }
    }

    handleInputChange = (event: React.FormEvent<HTMLInputElement>) => {
        const target = event.currentTarget;
        const value: boolean = target.checked;
        const name: string = target.name;

        switch(name) {
            case "isRelatedGraph":
                if(!Boolean(value)) {
                    this.setState({isCompareWithGeneral: false});
                }
                this.setState({isRelatedGraph: Boolean(value)});
                break;
            case "isCompareWithGeneral":
                if(Boolean(value)) {
                    this.setState({isRelatedGraph: true});
                }
                this.setState({isCompareWithGeneral: Boolean(value)});
                break;
            default:
                throw new  Error(`Strange input name: ${name}`);
        }
    };


    render() {
        const isRelatedGraph = this.state.isRelatedGraph;
        const isCompareWithGeneral = this.state.isCompareWithGeneral;
        const option = this.echartOption();
        const echartsSettings: SetOptionOpts = {replaceMerge: ["series"]}
        return (
            <>
                <>
                    <label>
                        Related data on the graph:
                        <input
                            name="isRelatedGraph"
                            type="checkbox"
                            checked={isRelatedGraph}
                            onChange={this.handleInputChange}
                        />
                    </label>
                    <br />
                    <label>
                        Compare with market data:
                        <input
                            name="isCompareWithGeneral"
                            type="checkbox"
                            checked={isCompareWithGeneral}
                            onChange={this.handleInputChange}
                        />
                    </label>
                </>

                <div>
                    <ReactECharts option={option}
                                  settings={echartsSettings}
                                  style={{height: '600px', width: '100%'}}/>
                </div>
            </>
        );
    }
}

export default GeneralDistribution;
