export function getTimingAll(data: any): any {
    return getDataset(data["time_between_actions_distribution"]);
}

export function getTimingBoxplotData(data: any): any {
    const actions: Array<number> = data["time_boxplot"]["action_num"];
    const data_boxplot: any = data["time_boxplot"]["data"];
    return {actions: actions, data: data_boxplot};
}

export function getHeatmapCohortData(data: any): any {

    const maxValue: number = data["cohort_data"]["max_retention_value"];
    const cohort_lifetime: Array<any> = data["cohort_data"]["cohort_lifetime"];
    const cohort_month_first: Array<any> = data["cohort_data"]["cohort_month_first"];
    const dataset: any = getDataset(data["cohort_data"]);

    return { x: cohort_lifetime, y: cohort_month_first, maxValue: maxValue ,data: dataset};
}

export function getGeneralDistribution(data: any): any  {
    const lost_users_after_first_action: number =  data["user_distribution_by_number_of_orders"]["lost_users_after_first_action"];
    const lost_users_after_first_action_related: string =
        (data["user_distribution_by_number_of_orders"]["lost_users_after_first_action_related"]*100).toFixed(0);
    const dataset: any = getDataset(data["user_distribution_by_number_of_orders"]);


    return {lost_users_after_first_action: lost_users_after_first_action,
            lost_users_after_first_action_related: lost_users_after_first_action_related,
            dataset: dataset};
}

function getDataset(data: any): any {
    return [data["columns"]].concat(data["data"]);
}

